import React from 'react'
import { Button, ButtonGroup } from 'components/Common'

export default class ButtonGroupDefault extends React.Component {
  render() {
    return (
      <ButtonGroup>
        <Button>Left</Button>
        <Button>Middle</Button>
        <Button>Right</Button>
      </ButtonGroup>
    )
  }
}



